function exchangeCityData(data) {
	return data.map((ele, index) => {
		if(ele.children && ele.children.length != 0) {
			ele.children = exchangeCityData(ele.children)
		}
		ele.value = ele.label
		ele.extra = index
		return ele
	})
}

